html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  margin: 0;
  padding: 0;
}

:root {
  --dark-cyan: hsl(185, 75%, 39%);
  --very-dark-blue: hsl(229, 23%, 23%);
  --dark-grayish-blue: hsl(227, 10%, 46%);
  --dark-gray: hsl(0, 0%, 59%);
}

:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-kumbh);
  margin: 0;
}

.hero {
  height: 100%;
  background-color: var(--dark-cyan);
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("../../../images/bg-pattern-top.svg");
  background-attachment: fixed;
  background-position: 2em -20em;
  background-repeat: no-repeat;
  background-size: 50em;
  height: 100%;
  width: 100%;
}
@media (max-width: 71.8125em) {
  .hero::before {
    background-size: 30em;
    background-position: 3em -10em;
  }
}
@media (max-width: 39.9375em) {
  .hero::before {
    background-size: 20em;
    background-position: 1em -10em;
  }
}
.hero::after {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-image: url("../../../images/bg-pattern-bottom.svg");
  background-attachment: fixed;
  background-position: 120% 300%;
  background-repeat: no-repeat;
  background-size: 50em;
}
@media (max-width: 71.8125em) {
  .hero::after {
    background-size: 40em;
    background-position: 120% 200%;
  }
}
@media (max-width: 39.9375em) {
  .hero::after {
    background-size: 33em;
    background-position: 500% 140%;
  }
}
.hero__container {
  background-color: white;
  width: 28em;
  z-index: 999;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}
.hero__container .bg_card {
  display: block;
  width: 100%;
}
.hero__container .card {
  position: absolute;
  top: 0%;
  left: 36%;
  margin: 8rem auto;
  text-align: center;
}
.hero__container .card .profile {
  border-radius: 50%;
  border: 5px solid white;
  margin-bottom: 0.5rem;
}
.hero__container .card .info {
  display: flex;
  align-items: center;
}
.hero__container .card .info h3 {
  margin-bottom: 0.1rem;
  margin-right: 0.3rem;
  font-size: 1.125rem;
  color: var(--very-dark-blue);
}
.hero__container .card .info p {
  color: var(--dark-gray);
}
.hero__container .card .location {
  color: var(--dark-gray);
}
.hero__container .line {
  width: 100%;
  height: 0.08rem;
  background-color: rgba(106, 111, 129, 0.2);
  margin-top: 10rem;
}
.hero__container .stats {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 1.5rem 4rem 1.5rem 3rem;
}
.hero__container .stats .stat {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__container .stats .stat h2 {
  font-size: 1.125rem;
}
.hero__container .stats .stat p {
  color: var(--dark-gray);
}